    if (astrologyResults.career.strengths) {
      html += `<p><strong>${translations.careerStrengths || ''}:</strong> ${astrologyResults.career.strengths}</p>`
    }
    if (astrologyResults.career.suggestions && Array.isArray(astrologyResults.career.suggestions)) {
      html += `<p><strong>${translations.careerSuggestions || ''}:</strong> ${astrologyResults.career.suggestions.join(', ')}</p>`
    }
    html += `</div>`
